"use client";

import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { useToast } from "@/components/ui/use-toast";
import { PageHeader } from "@/components/ui/page-header";
import { CalendarDays, BarChart, BookOpen } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";
import ResultsTable from "../../results/components/ResultsTable";
import SubjectAnalysis from "../../results/components/SubjectAnalysis";
import ResultsFilters from "../../results/components/ResultsFilters";
import ScoreChart from "../../results/components/ScoreChart";

interface QuizResultItem {
  attemptId: string;
  quizId: string;
  quizTitle: string;
  quizDescription: string | null;
  className: string | null;
  category: string | null;
  score: number;
  totalQuestions: number;
  correctQuestions: number;
  completedAt: string;
  timeTakenMinutes: number | null;
  feedbackAvailable: boolean;
}

interface PaginationData {
  currentPage: number;
  totalPages: number;
  totalItems: number;
  pageSize: number;
}

export default function ResultsPage() {
  const searchParams = useSearchParams();
  const { toast } = useToast();
  
  const [results, setResults] = useState<QuizResultItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [pagination, setPagination] = useState<PaginationData>({
    currentPage: 1,
    totalPages: 1,
    totalItems: 0,
    pageSize: 10
  });
  const [timeFrame, setTimeFrame] = useState(searchParams.get("timeFrame") || "last30days");
  const [subject, setSubject] = useState(searchParams.get("subject") || null);
  
  useEffect(() => {
    const fetchResults = async () => {
      setLoading(true);
      try {
        const params = new URLSearchParams();
        params.append("page", "1");
        params.append("pageSize", "10");
        
        if (timeFrame) {
          params.append("timeFrame", timeFrame);
        }
        
        if (subject) {
          params.append("subject", subject);
        }
        
        const response = await fetch(`/api/users/me/quiz-results?${params.toString()}`);
        
        if (!response.ok) {
          throw new Error("Failed to fetch results");
        }
        
        const data = await response.json();
        
        if (data.success) {
          setResults(data.data.results);
          setPagination(data.data.pagination);
        } else {
          throw new Error(data.message || "Failed to fetch results");
        }
      } catch (error) {
        console.error("Error fetching results:", error);
        toast({
          title: "Error",
          description: error instanceof Error ? error.message : "Failed to load results",
          variant: "destructive",
        });
      } finally {
        setLoading(false);
      }
    };
    
    fetchResults();
  }, [timeFrame, subject, toast]);

  return (
    <div className="space-y-6">
      <PageHeader
        heading="Kết quả học tập"
        text="Xem kết quả và phân tích hiệu suất học tập của bạn"
      />
      
      <ResultsFilters
        timeFrame={timeFrame}
        subject={subject}
        onTimeFrameChange={setTimeFrame}
        onSubjectChange={setSubject}
      />
      
      <Tabs defaultValue="overview" className="space-y-4">
        <TabsList>
          <TabsTrigger value="overview">Tổng quan</TabsTrigger>
          <TabsTrigger value="results">Kết quả chi tiết</TabsTrigger>
          <TabsTrigger value="subjects">Phân tích theo môn học</TabsTrigger>
        </TabsList>
        
        <TabsContent value="overview" className="space-y-4">
          <div className="grid gap-4 md:grid-cols-3">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Điểm trung bình
                </CardTitle>
                <BarChart className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                {loading ? (
                  <Skeleton className="h-8 w-20" />
                ) : (
                  <div className="text-2xl font-bold">
                    {results.length > 0
                      ? (results.reduce((acc, item) => acc + item.score, 0) / results.length).toFixed(1) + "%"
                      : "N/A"}
                  </div>
                )}
              </CardContent>
            </Card>
            
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Bài kiểm tra đã hoàn thành
                </CardTitle>
                <BookOpen className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                {loading ? (
                  <Skeleton className="h-8 w-20" />
                ) : (
                  <div className="text-2xl font-bold">
                    {pagination.totalItems}
                  </div>
                )}
              </CardContent>
            </Card>
            
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Thời gian phản hồi
                </CardTitle>
                <CalendarDays className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                {loading ? (
                  <Skeleton className="h-8 w-20" />
                ) : (
                  <div className="text-2xl font-bold">
                    {results.length > 0
                      ? Math.round(
                          results.reduce(
                            (acc, item) => acc + (item.timeTakenMinutes || 0),
                            0
                          ) / results.length
                        ) + " phút"
                      : "N/A"}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
          
          <Card>
            <CardHeader>
              <CardTitle>Biểu đồ điểm số</CardTitle>
            </CardHeader>
            <CardContent>
              {loading ? (
                <div className="w-full aspect-[3/2]">
                  <Skeleton className="h-full w-full" />
                </div>
              ) : results.length > 0 ? (
                <ScoreChart results={results} />
              ) : (
                <div className="text-center py-8 text-muted-foreground">
                  Chưa có dữ liệu điểm số nào trong khoảng thời gian này.
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="results">
          {loading ? (
            <Card>
              <CardContent className="pt-6">
                <div className="space-y-2">
                  <Skeleton className="h-8 w-full" />
                  <Skeleton className="h-8 w-full" />
                  <Skeleton className="h-8 w-full" />
                  <Skeleton className="h-8 w-full" />
                  <Skeleton className="h-8 w-full" />
                </div>
              </CardContent>
            </Card>
          ) : results.length > 0 ? (
            <ResultsTable results={results} pagination={pagination} />
          ) : (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12 text-center">
                <div className="rounded-full bg-muted p-3 text-foreground mb-4">
                  <BookOpen className="h-6 w-6" />
                </div>
                <h3 className="text-lg font-medium">Không có kết quả nào</h3>
                <p className="text-muted-foreground mt-1 max-w-md">
                  Không có kết quả bài kiểm tra nào trong khoảng thời gian đã chọn.
                </p>
                <Button asChild className="mt-6">
                  <a href="/dashboard/student/quizzes">Xem bài kiểm tra có sẵn</a>
                </Button>
              </CardContent>
            </Card>
          )}
        </TabsContent>
        
        <TabsContent value="subjects">
          {loading ? (
            <Card>
              <CardContent className="pt-6">
                <Skeleton className="h-64 w-full" />
              </CardContent>
            </Card>
          ) : results.length > 0 ? (
            <SubjectAnalysis results={results} />
          ) : (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12 text-center">
                <div className="rounded-full bg-muted p-3 text-foreground mb-4">
                  <BarChart className="h-6 w-6" />
                </div>
                <h3 className="text-lg font-medium">Không có dữ liệu phân tích</h3>
                <p className="text-muted-foreground mt-1 max-w-md">
                  Hoàn thành thêm bài kiểm tra để xem thống kê theo môn học.
                </p>
              </CardContent>
            </Card>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
} 