"use client";

import { useState, useEffect } from "react";
import { 
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue
} from "@/components/ui/select";
import { Button } from "@/components/ui/button";
import { Calendar, Filter, X } from "lucide-react";
import { cn } from "@/lib/utils";

interface ResultsFiltersProps {
  timeFrame: string;
  subject: string | null;
  onTimeFrameChange: (value: string) => void;
  onSubjectChange: (value: string | null) => void;
}

export default function ResultsFilters({
  timeFrame = "last30days",
  subject,
  onTimeFrameChange,
  onSubjectChange
}: ResultsFiltersProps) {
  const [subjects, setSubjects] = useState<string[]>([]);
  const [loading, setLoading] = useState(false);
  
  // Determine if we have any active filters
  const hasActiveFilters = !!subject || timeFrame !== "last30days";
  
  // Fetch available subjects for filtering
  useEffect(() => {
    const fetchSubjects = async () => {
      setLoading(true);
      try {
        const response = await fetch("/api/subjects");
        if (response.ok) {
          const data = await response.json();
          if (data.success) {
            setSubjects(data.data || []);
          }
        }
      } catch (error) {
        console.error("Error fetching subjects:", error);
      } finally {
        setLoading(false);
      }
    };
    
    fetchSubjects();
  }, []);
  
  // Handler for time frame filter changes
  const handleTimeFrameChange = (value: string) => {
    onTimeFrameChange(value);
  };
  
  // Handler for subject filter changes
  const handleSubjectChange = (value: string) => {
    onSubjectChange(value === "all" ? null : value);
  };
  
  // Handler to reset all filters
  const handleResetFilters = () => {
    onTimeFrameChange("last30days");
    onSubjectChange(null);
  };
  
  return (
    <div className="flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
      <div className="flex flex-wrap items-center gap-2">
        <div className="flex items-center gap-2">
          <Calendar className="h-4 w-4 text-muted-foreground" />
          <span className="text-sm font-medium">Khoảng thời gian:</span>
          <Select 
            value={timeFrame} 
            onValueChange={handleTimeFrameChange}
          >
            <SelectTrigger className="w-32 sm:w-40 h-9">
              <SelectValue placeholder="Chọn thời gian" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="last7days">7 ngày gần đây</SelectItem>
              <SelectItem value="last30days">30 ngày gần đây</SelectItem>
              <SelectItem value="last90days">90 ngày gần đây</SelectItem>
              <SelectItem value="last6months">6 tháng gần đây</SelectItem>
              <SelectItem value="lastYear">1 năm gần đây</SelectItem>
              <SelectItem value="allTime">Tất cả thời gian</SelectItem>
            </SelectContent>
          </Select>
        </div>
        
        {subjects.length > 0 && (
          <div className="flex items-center gap-2">
            <Filter className="h-4 w-4 text-muted-foreground" />
            <span className="text-sm font-medium">Môn học:</span>
            <Select 
              value={subject || "all"} 
              onValueChange={handleSubjectChange}
            >
              <SelectTrigger className="w-32 sm:w-40 h-9">
                <SelectValue placeholder="Tất cả môn học" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Tất cả môn học</SelectItem>
                {subjects.map((subj) => (
                  <SelectItem key={subj} value={subj}>
                    {subj}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        )}
      </div>
      
      {hasActiveFilters && (
        <Button 
          variant="outline" 
          size="sm"
          onClick={handleResetFilters}
          className={cn(
            "h-9 px-3 gap-1.5", 
            subject && "border-primary/40 hover:border-primary"
          )}
        >
          <X className="h-3.5 w-3.5" />
          Xóa bộ lọc
        </Button>
      )}
    </div>
  );
} 