"use client";

import React, { useEffect, useRef, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { format } from 'date-fns';
import { vi } from 'date-fns/locale';
import dynamic from 'next/dynamic';

// Import types only
import type { ChartData, ChartOptions } from 'chart.js';

// Dynamically import the Line component from react-chartjs-2
const Line = dynamic(
  () => import('react-chartjs-2').then((mod) => mod.Line),
  { ssr: false }
);

// Initialize chart.js with required components
const initChartJs = async () => {
  const { Chart, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } = 
    await import('chart.js');
  
  Chart.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
  );
  
  return Chart;
};

interface QuizResultItem {
  attemptId: string;
  quizId: string;
  quizTitle: string;
  quizDescription: string | null;
  className: string | null;
  category: string | null;
  score: number;
  totalQuestions: number;
  correctQuestions: number;
  completedAt: string;
  timeTakenMinutes: number | null;
  feedbackAvailable: boolean;
}

interface ScoreChartProps {
  results: QuizResultItem[];
}

export function ScoreChart({ results }: ScoreChartProps) {
  const [chartData, setChartData] = useState<ChartData<'line'>>({
    labels: [],
    datasets: [],
  });
  
  const [chartOptions, setChartOptions] = useState<ChartOptions<'line'>>({});
  const [sortedResults, setSortedResults] = useState<QuizResultItem[]>([]);
  const [chartJsLoaded, setChartJsLoaded] = useState(false);

  // Initialize Chart.js
  useEffect(() => {
    initChartJs().then(() => {
      setChartJsLoaded(true);
    });
  }, []);

  useEffect(() => {
    if (!results || results.length === 0 || !chartJsLoaded) {
      return;
    }

    // Sort results by completion date
    const sorted = [...results].sort(
      (a, b) => new Date(a.completedAt).getTime() - new Date(b.completedAt).getTime()
    );
    setSortedResults(sorted);

    // Prepare data for the chart
    const labels = sorted.map(result => 
      format(new Date(result.completedAt), 'dd/MM/yyyy')
    );
    
    const scores = sorted.map(result => result.score);

    // Set chart data
    setChartData({
      labels,
      datasets: [
        {
          label: 'Điểm số',
          data: scores,
          borderColor: 'rgb(59, 130, 246)',
          backgroundColor: 'rgba(59, 130, 246, 0.1)',
          borderWidth: 2,
          tension: 0.3,
          fill: true,
          pointBackgroundColor: 'rgb(59, 130, 246)',
          pointRadius: 4,
          pointHoverRadius: 6,
        },
      ],
    });

    // Set chart options
    setChartOptions({
      responsive: true,
      maintainAspectRatio: false,
      scales: {
        y: {
          beginAtZero: true,
          max: 100,
          title: {
            display: true,
            text: 'Điểm số (%)',
            color: '#64748b',
          },
          grid: {
            color: 'rgba(226, 232, 240, 0.5)',
          },
          ticks: {
            color: '#64748b',
          },
        },
        x: {
          grid: {
            display: false,
          },
          ticks: {
            color: '#64748b',
            maxRotation: 45,
            minRotation: 45,
          },
        },
      },
      plugins: {
        tooltip: {
          callbacks: {
            title: function(tooltipItems) {
              const idx = tooltipItems[0].dataIndex;
              return sorted[idx].quizTitle;
            },
            afterTitle: function(tooltipItems) {
              const idx = tooltipItems[0].dataIndex;
              return `Môn học: ${sorted[idx].category || "Không phân loại"}`;
            },
            label: function(context) {
              return `Điểm số: ${context.parsed.y}%`;
            },
            afterLabel: function(context) {
              const idx = context.dataIndex;
              const result = sorted[idx];
              const formattedDate = format(
                new Date(result.completedAt), 
                "HH:mm 'ngày' dd/MM/yyyy",
                { locale: vi }
              );
              return [
                `Hoàn thành: ${formattedDate}`,
                `Câu đúng: ${result.correctQuestions}/${result.totalQuestions}`
              ];
            },
          }
        },
        legend: {
          display: false,
        },
      },
    });

  }, [results, chartJsLoaded]);

  if (!results || results.length === 0) {
    return (
      <Card>
        <CardContent className="text-center py-10">
          <p className="text-muted-foreground">Không có dữ liệu để hiển thị biểu đồ</p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Điểm số theo thời gian</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="h-[300px]">
          {chartJsLoaded && <Line options={chartOptions} data={chartData} />}
        </div>
      </CardContent>
    </Card>
  );
} 