"use client";

import { useMemo } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { ScrollArea } from "@/components/ui/scroll-area";

interface QuizResultItem {
  attemptId: string;
  quizId: string;
  quizTitle: string;
  quizDescription: string | null;
  className: string | null;
  category: string | null;
  score: number;
  totalQuestions: number;
  correctQuestions: number;
  completedAt: string;
  timeTakenMinutes: number | null;
  feedbackAvailable: boolean;
}

interface SubjectAnalysisProps {
  results: QuizResultItem[];
}

interface SubjectStats {
  name: string;
  quizCount: number;
  totalQuestions: number;
  correctQuestions: number;
  averageScore: number;
  bestScore: number;
  worstScore: number;
}

export default function SubjectAnalysis({ results }: SubjectAnalysisProps) {
  // Group and analyze results by subject
  const subjectStats = useMemo(() => {
    const subjectMap = new Map<string, SubjectStats>();
    
    // Process each result
    results.forEach(result => {
      const subjectName = result.category || "Không phân loại";
      
      if (!subjectMap.has(subjectName)) {
        subjectMap.set(subjectName, {
          name: subjectName,
          quizCount: 0,
          totalQuestions: 0,
          correctQuestions: 0,
          averageScore: 0,
          bestScore: 0,
          worstScore: 100
        });
      }
      
      const stats = subjectMap.get(subjectName)!;
      
      // Update stats
      stats.quizCount += 1;
      stats.totalQuestions += result.totalQuestions;
      stats.correctQuestions += result.correctQuestions;
      stats.averageScore = ((stats.averageScore * (stats.quizCount - 1)) + result.score) / stats.quizCount;
      stats.bestScore = Math.max(stats.bestScore, result.score);
      stats.worstScore = Math.min(stats.worstScore, result.score);
    });
    
    // Convert to array and sort by average score (descending)
    return Array.from(subjectMap.values())
      .sort((a, b) => b.averageScore - a.averageScore);
      
  }, [results]);
  
  if (subjectStats.length === 0) {
    return (
      <Card>
        <CardContent className="text-center py-10">
          <p className="text-muted-foreground">Không có dữ liệu phân tích môn học</p>
        </CardContent>
      </Card>
    );
  }
  
  return (
    <div className="space-y-6">
      <Tabs defaultValue="performance">
        <TabsList>
          <TabsTrigger value="performance">Hiệu suất theo môn</TabsTrigger>
          <TabsTrigger value="comparison">So sánh môn học</TabsTrigger>
        </TabsList>
        
        <TabsContent value="performance" className="pt-4">
          <ScrollArea className="h-[500px]">
            <div className="space-y-4 pr-4">
              {subjectStats.map(subject => (
                <Card key={subject.name} className="overflow-hidden">
                  <CardHeader className="pb-2">
                    <CardTitle className="text-base font-medium">{subject.name}</CardTitle>
                  </CardHeader>
                  
                  <CardContent className="pb-4">
                    <div className="flex flex-col space-y-4">
                      <div className="space-y-1.5">
                        <div className="flex justify-between text-sm">
                          <span>Điểm trung bình</span>
                          <span className="font-medium">{subject.averageScore.toFixed(1)}%</span>
                        </div>
                        <Progress value={subject.averageScore} className="h-2" />
                      </div>
                      
                      <div className="grid grid-cols-2 gap-4 text-sm">
                        <div className="space-y-1">
                          <span className="text-muted-foreground">Số bài kiểm tra</span>
                          <p className="font-medium">{subject.quizCount}</p>
                        </div>
                        
                        <div className="space-y-1">
                          <span className="text-muted-foreground">Tỷ lệ trả lời đúng</span>
                          <p className="font-medium">
                            {subject.correctQuestions}/{subject.totalQuestions} ({Math.round(subject.correctQuestions / subject.totalQuestions * 100)}%)
                          </p>
                        </div>
                        
                        <div className="space-y-1">
                          <span className="text-muted-foreground">Điểm cao nhất</span>
                          <p className="font-medium text-green-600 dark:text-green-500">{subject.bestScore.toFixed(1)}%</p>
                        </div>
                        
                        <div className="space-y-1">
                          <span className="text-muted-foreground">Điểm thấp nhất</span>
                          <p className="font-medium text-red-600 dark:text-red-500">{subject.worstScore.toFixed(1)}%</p>
                        </div>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </ScrollArea>
        </TabsContent>
        
        <TabsContent value="comparison" className="pt-4">
          <Card>
            <CardHeader>
              <CardTitle>So sánh điểm trung bình theo môn học</CardTitle>
            </CardHeader>
            
            <CardContent>
              <div className="space-y-6">
                {subjectStats.map(subject => (
                  <div key={subject.name} className="space-y-1.5">
                    <div className="flex justify-between text-sm font-medium">
                      <span>{subject.name}</span>
                      <span>{subject.averageScore.toFixed(1)}%</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Progress 
                        value={subject.averageScore}
                        className="h-2" 
                      />
                      <span className="text-xs text-muted-foreground w-12">
                        {subject.quizCount} quiz
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
} 